import classes from './Post.module.css'
import React from 'react'

function Post({post, username}) {
    return (
        <div className={classes.post}>
            <div>
                <div className={classes.header}>
                    <div className={classes.name}>
                        <img src={post.avatar} alt='avatar' className={classes.avatar}/>
                        <div className={classes.nickname}>
                            {post.username}
                        </div>
                    </div>
                    <div className={classes.circles}>
                        ...
                    </div>
                </div>
                
                <img className={classes.postImg} src={post.imageUrl} alt=''/>
                <div className={classes.postText}><strong>{post.username}</strong> {post.caption}
                </div>
            </div>

        </div>
    )
}

export default Post
