const GET_POSTS = 'GET_POSTS'

const initialState = {
    posts: []
}



export const reducers = (state = initialState, action) => {
    switch (action.type) {
        case GET_POSTS:
            return {
                ...state,
                posts: action.posts
            }
        default:
            return state;
    }
}


export const setPosts = (posts) => {
    return {
        type: GET_POSTS,
        payload: posts
    }
}